const formatDate = (date: string, isUpdateDate = false) => {
  const options: Intl.DateTimeFormatOptions = {
    year: "2-digit",
    month: "numeric",
    day: "numeric",
  };

  const formatted = new Date(date).toLocaleDateString("fr-FR", options);
  return isUpdateDate ? `maj le ${formatted}` : `rédigé le ${formatted}`;
};

const hasBeenUpdated = (creationDate: string, editedDate: string) =>
  editedDate && new Date(creationDate) < new Date(editedDate);

export { formatDate, hasBeenUpdated };
